import ClassicEditor from '@ckeditor/ckeditor5-editor-classic/src/classiceditor.js';
import Autosave from '@ckeditor/ckeditor5-autosave/src/autosave.js';
import Bold from '@ckeditor/ckeditor5-basic-styles/src/bold.js';
import Essentials from '@ckeditor/ckeditor5-essentials/src/essentials.js';
import HorizontalLine from '@ckeditor/ckeditor5-horizontal-line/src/horizontalline.js';
import Italic from '@ckeditor/ckeditor5-basic-styles/src/italic.js';
import List from '@ckeditor/ckeditor5-list/src/list.js';
import Paragraph from '@ckeditor/ckeditor5-paragraph/src/paragraph.js';
import PasteFromOffice from '@ckeditor/ckeditor5-paste-from-office/src/pastefromoffice.js';
import RemoveFormat from '@ckeditor/ckeditor5-remove-format/src/removeformat.js';
import Underline from '@ckeditor/ckeditor5-basic-styles/src/underline.js';
import WProofreader from '@webspellchecker/wproofreader-ckeditor5/src/wproofreader.js';

class Editor extends ClassicEditor {}

var wproofreader_protocol = window.wproofreaderProtocol;
var wproofreader_host = window.wproofreaderHost;
var wproofreader_port = window.wproofreaderPort;
var wproofreader_url = wproofreader_protocol + '://' + wproofreader_host + (wproofreader_port ? ':' + wproofreader_port : '');

// Plugins to include in the build.
Editor.builtinPlugins = [
	Autosave,
	Bold,
	Essentials,
	HorizontalLine,
	Italic,
	List,
	Paragraph,
	PasteFromOffice,
	RemoveFormat,
	Underline,
	WProofreader
];

// Editor configuration.
Editor.defaultConfig = {
	toolbar: {
		items: [
			'bold',
			'italic',
			'underline',
			'|',
			'bulletedList',
			'numberedList',
			'|',
			'undo',
			'redo',
			'|',
			'removeFormat',
			'|',
			'wproofreader'
		]
	},
	language: 'en-gb',
	wproofreader: {
		lang: 'en_GB',
		serviceProtocol: wproofreader_protocol,
		serviceHost: wproofreader_host,
		servicePort: wproofreader_port || '80',
		servicePath: 'wscservice/api',
		removeBranding: true,
		settingsSections: [
			'general',
			'options'	
		],
		srcUrl: wproofreader_url + '/wscservice/wscbundle/wscbundle.js'
	}
};

export default Editor;
